export default {
  props: ['query'],
  data () {
    return {
      keyword: this.query
    }
  },
  methods: {

    confirmHandler () {
      // 如果输入内容不为空的话，$emit事件
      if (this.keyword) {
        this.$emit('search', this.keyword)
      }
    }
  },
  watch: {
    query (newValue) {
      this.keyword = newValue
    }
  }
}
